.creative-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  background: var(--color-guide-community-0-e-1428, #070a15);
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 5rem;
}
.creative-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40vw;
  gap: 1em;
  height: 100vh;
}
.creative-text-container {
  display: flex;
  width: 40vw;
  height: auto;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin: 0 0 0 1rem;
}
.creative-headings {
  color: var(--color-guide-common-white, #fff);
  font-family: Open Sans;
  font-size: 65px;
  font-style: normal;
  font-weight: 800;
}
.creative-headings .creative-heading-col {
  color: #7b9e89;
  font-family: Open Sans;
  font-size: 75px;
  font-style: normal;
  font-weight: 800;
  line-height: 98.682%;
}
.creative-para {
  width: 40vw;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.5rem;
  font-weight: 400;
}

.creative-cards-container {
  display: flex;
  width: 40vw;
  height: auto;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  /* overflow: hidden; */
}
.creative-cards {
  display: flex;
  align-self: stretch;
  border-radius: 5px;
  border: 2px solid var(--color-guide-secondary-50, #b0a2db);
  width: 35vw;
  padding: 20px;
  align-items: center;
  gap: 10px;
}
.creative-cards span {
  color: #fff;
  font-family: Open Sans;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.creative-image-container {
  width: 40vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.creative-image-container img {
  width: 35vw;
  height: 75vh;
  border-radius: 10px;
}

@media screen and (max-width: 1367px) {
  .creative-container {
    gap: 3rem;
  }

  .creative-headings {
    font-size: 50px;
  }
  .creative-headings .creative-heading-col {
    font-size: 50px;
  }
  .creative-para {
    font-size: 1rem;
  }

  .creative-cards-container {
    gap: 10px;
  }
  .creative-cards {
    padding: 12px;
  }
  .creative-cards span {
    font-size: 1rem;
  }
  .creative-image-container img {
    height: 70vh;
  }
  .creative-image-container img {
    width: 35vw;
    height: 40vw;
    border-radius: 10px;
    max-width: 500px;
  }
}

@media screen and (max-width: 850px) {
