    font-family: Open Sans;
    font-size: 45px;
    font-style: normal;
    font-weight: 800;
    line-height: 98.682%;
  }
  .creative-para {
    width: 80vw;
    color: rgba(255, 255, 255, 0.8);
    font-size: 1.2rem;
    font-weight: 400;
  }

  .creative-cards-container {
    display: flex;
    width: 90vw;
    height: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 10px 0 10px 0;
    /* overflow: hidden; */
  }
  .creative-cards {
    display: flex;
    width: auto;
    /* padding: 15px; */
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 10px;
  }
  .creative-cards span {
    color: #fff;
    font-family: Open Sans;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  .creative-right {
    height: auto;
  }
  .creative-image-container {
    width: 90vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
  }
  .creative-image-container img {
    width: 90%;
    height: auto;
    aspect-ratio: 1/1;
    border-radius: 10px;
    max-width: 500px;
  }
}
