.footer-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  background: var(--color-guide-community-0-e-1428, #070a15);
  justify-content: center;
  align-items: self-end;
  flex-wrap: wrap;
  gap: 2rem;
  padding: 0 0 70px 0;
  /* border : 1px solid red; */
}

.footer-headings {
  width: 75vw;
  height: auto;
  display: flex;
  align-items: center;
  color: #fff;
  text-align: center;
  font-family: Open Sans;
  gap: 0px;
  font-size: 75px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  /* margin: 0 0 5rem 0; */
  align-content: center;
  flex-direction: column;
  justify-content: center;
}
.footer-links {
  width: 80vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-top:2rem; */
}
.footer-links-cols {
  width: 15vw;
  color: #fff;
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer-line {
  width: 90vw;
  height: 2px;
  background: #1a1f32;
}
.footer-copyright {
  color: #fff;
  font-family: Open Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  margin: 0 0 1rem 0;
}

.footer-links-cols a {
  margin: 0;
  cursor: pointer;
}
.footer-whatsapp {
  cursor: pointer;
  display: flex;
  padding: 25px 15px;
  width: 30vw;
  justify-content: center;
  align-items: center;
  border-radius: 500px;
  background: linear-gradient(94deg, #fbb913 5.82%, #4dcf41 94.4%);
  box-shadow: 0px 5px 0px 0px rgba(255, 255, 255, 0.25) inset,
    0px -6px 0px 0px rgba(33, 44, 28, 0.25) inset,
    0px 0px 0px 12px rgba(255, 255, 255, 0.05),
    0px 0px 96px -16px rgba(99, 177, 87, 0.75);
  gap: 1.5rem;
  margin-top: 40px;
}

.footer-whatsapp span {
  font-size: 20px;
  color:#070a15
}
.footer-whatsapp svg {
  width: 2vw;
  height: 2vw;
}

@media screen and (max-width: 1367px) {
  .footer-container {
    height: 100vh;
  }
  .footer-headings {
    width: 75vw;
    font-size: 50px;
    height: auto;
  }

  .footer-links-cols {
    width: 15vw;
    color: #fff;
    font-family: Open Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    display: flex;
    text-align: start;
    flex-direction: column;
    gap: 20px;
  }
  .footer-line {
    width: 90vw;
    height: 2px;
    background: #1a1f32;
  }
  .footer-copyright {
    color: #fff;
    font-family: Open Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    margin: 0 0 1rem 0;
  }

  .footer-whatsapp {
    display: flex;
    padding: 15px;
    justify-content: center;
    align-items: center;
    font-size: 8px;
    gap: 10px;
    
  }
}

