@media screen and (max-width: 500px) {
  .footer-container {
    height: auto;
    gap: 15px;
  }
  .footer-whatsapp {
    margin: 15px 0;
    gap: 10px;
    
  }
  .footer-whatsapp span {
    font-size: 15px;
    width: inherit;
  }
  .footer-whatsapp svg {
    height: 2.5rem;
    width: 2rem;
  }
  .footer-headings {
    font-size: 30px;
  }
  .footer-links {
    height: auto;
    gap: 13px;
  }
